body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #131313;
}







.footer{
   .input-wrapper{
     display: flex;
   }
}

.game{
  position: relative;
  max-width: 768px;
  width: 100%;
  height: 600px;
  margin: 0 auto;
  padding: 0px 15px;

  .restart{
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    cursor: pointer;
    transition: 0.2s linear;
    &:hover{
     transform: rotate(-90deg);
    }
   img{
     max-width: 24px;
   }
  }


  &-title{
    text-align: center;
    padding-bottom: 15px;
    border-bottom: 1px solid #cfcfcf;
  }

  &-field{
    height: 100%;
  }

  .word-row{
    display: flex;
    width: 100%;
    .letter{
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: bold;
      text-align: center;
      text-transform: uppercase;
      border: 1px solid #cfcfcf;
      min-height: 100px;
      width: 100%;
      font-size: 20px;
      margin: 5px;
      border-radius: 3px;
      
    }
  }

  .row{
    width: 100%;
    display: flex;
  }

  .word-input{
    display: block;
    width: 100%;
    border: 1px solid #cfcfcf;
    padding: 10px;
    outline: none;
  }


}

.letter-correct-place{
  background: #79B851;
}

.letter-correct{
  background: #F3C237;
}

.letter-incorrect{
  background: #A4AEC4;
}

.send-word{
  width: 100%;
  margin-top: 10px;
  border: none;
  outline: none;
  background: #F3C237;
  color: #fff;
  font-weight: bold;
  padding: 15px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.2s linear;
  margin-bottom: 40px;
  border-radius: 3px;
  &:hover{
    background: darken(#F3C237, 10%);
  }
}


.game-over{
  max-width: 400px;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  border-radius: 3px;
  border: 1px solid #cfcfcf;
  padding: 25px;
  &-title{
    font-size: 28px;
    font-weight: bold;
  }
  &-text{
    margin-top: 10px;
  }

  &-btns{
    display: flex;
    align-items: center;
    button{
      width: 100%;
      height: 50px;
      color: #fff;
      font-weight: bold;
      transition: 0.2s linear;
      margin: 0px 5px;
      &:first-child{
        background: #79B851;
        &:hover{
          background: darken(#79B851, 10%);
        }
      }
      &:last-child{
        background: #F3C237;

        &:hover{
          background: darken(#F3C237,10%);

        }
      }
      font-size: 20px;
      border: none;
      cursor: pointer;
    }
  }
}

@media(max-width: 768px){
  .game{
    max-width: 320px;
  }
  .footer{
    padding: 15px;
    background: #fff;
    position: fixed !important;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 320px;
    .send-word{
      margin-bottom: 0;
    }
    
  }

  .game .word-row .letter{
    min-height: 50px;
  }

  .game-over{
    padding: 10px;
    max-width: 300px;
  }
  .game-over-btns{
    flex-direction: column;
    button{
      &:first-child{
        margin-bottom: 10px;
      }
    }
  }

}